.muze-legend-selection-box {
  fill: #777777;
  fill-opacity: 0.3;
  stroke: #fff;
  cursor: all-scroll;
  pointer-events: all; }

.muze-legend-box, .muze-legend-title, .muze-legend-item-info {
  font-family: 'Source Sans Pro',"Helvetica Neue",Helvetica,Arial,sans-serif; }

.muze-legend-box {
  line-height: 10px !important;
  height: 100px;
  display: inline-block; }

.muze-legend-title {
  display: table;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  float: left;
  text-align: left;
  font-size: 14px;
  height: 30px;
  fill: #5F5F5F;
  font-weight: 600; }

.muze-legend-title-text {
  display: table-cell;
  vertical-align: middle; }

.muze-legend-overflow {
  text-align: center; }

.muze-legend-icon-svg {
  cursor: pointer; }

.muze-legend-icon {
  cursor: pointer; }

.muze-overflow-y {
  overflow-y: scroll; }

.muze-overflow-x {
  overflow-x: scroll; }

.muze-legend-fadeout {
  fill-opacity: 0.3;
  opacity: 0.3; }

.muze-legend-brighten {
  fill-opacity: 1; }

.muze-legend-item-info {
  float: left;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  cursor: pointer;
  font-size: 12px; }

.muze-legend-item-info.center {
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center; }

.muze-legend-item-info.start {
  -webkit-box-pack: start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start; }

.muze-legend-item-info.end {
  -webkit-box-pack: end;
  -webkit-justify-content: flex-end;
          justify-content: flex-end; }
