.muze-tooltip-box {
  opacity: 0.9;
  border: 1px solid rgba(151, 151, 151, 0.19);
  background: #fbfbfb;
  -webkit-box-shadow: 2px 2px 3px 0px rgba(211, 211, 211, 0.5);
          box-shadow: 2px 2px 3px 0px rgba(211, 211, 211, 0.5);
  font-size: 12px;
  color: #5f5f5f;
  -webkit-border-radius: 1px;
          border-radius: 1px;
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 4px;
  display: inline-block;
  pointer-events: none;
  z-index: 9999999999;
  text-align: left; }

.muze-tooltip-row {
  padding-bottom: 8px;
  overflow: auto; }

.muze-tooltip-row:last-child {
  padding-bottom: 4px; }

.muze-tooltip-value {
  font-weight: 600;
  float: right;
  margin-left: 10px; }

.muze-tooltip-connectors path {
  stroke-width: 2px;
  stroke: #c7c7be;
  stroke-linejoin: round;
  stroke-linecap: round; }

.muze-tooltip-arrow-bottom {
  border-top: 10px solid #fbfbfb;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  right: 0; }

.muze-tooltip-arrow-right {
  border-left: 10px solid #fbfbfb;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  right: -10px; }

.muze-tooltip-arrow-left {
  border-right: 10px solid #fbfbfb;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  right: 0;
  left: -10px; }

.muze-tooltip-arrow-top {
  border-bottom: 10px solid #fbfbfb;
  border-left: 5px solid transparent;
  border-right: 5px solid transparent;
  right: 0; }

.muze-tooltip-background-arrow-top {
  border-bottom: 12px solid rgba(151, 151, 151, 0.19);
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  top: 0%;
  right: 0; }

.muze-tooltip-background-arrow-bottom {
  border-top: 12px solid rgba(151, 151, 151, 0.19);
  border-left: 6px solid transparent;
  border-right: 6px solid transparent;
  top: 102%;
  right: 0; }

.muze-tooltip-background-arrow-right {
  border-left: 12px solid rgba(151, 151, 151, 0.19);
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  right: -12px; }

.muze-tooltip-background-arrow-left {
  border-right: 12px solid rgba(151, 151, 151, 0.19);
  border-top: 6px solid transparent;
  border-bottom: 6px solid transparent;
  right: 0;
  left: -12px; }

.muze-tooltip-content-container {
  float: none !important;
  padding-left: 8px;
  padding-right: 8px;
  padding-top: 4px; }

.muze--tooltip-value {
  font-weight: bold; }

.muze-tooltip-table-tbody td {
  padding: 3px; }

.muze-tooltip-table-row-0 {
  font-weight: bold; }
