.muze-scroll-bar {
  background: #f8f8f8;
  position: relative;
  font-size: 10px; }

.muze-scroll-arrow {
  float: left;
  cursor: pointer;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-align: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-box-pack: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: #a2a2a2;
  color: white; }

.muze-scroll-arrow-left {
  height: 100%;
  width: 20px;
  -webkit-border-top-right-radius: 3px;
          border-top-right-radius: 3px;
  -webkit-border-bottom-right-radius: 3px;
          border-bottom-right-radius: 3px; }

.muze-scroll-arrow-right {
  height: 100%;
  width: 20px;
  -webkit-border-top-left-radius: 3px;
          border-top-left-radius: 3px;
  -webkit-border-bottom-left-radius: 3px;
          border-bottom-left-radius: 3px; }

.muze-scroll-arrow-top {
  width: 100%;
  height: 20px;
  -webkit-border-bottom-left-radius: 3px;
          border-bottom-left-radius: 3px;
  -webkit-border-bottom-right-radius: 3px;
          border-bottom-right-radius: 3px; }

.muze-scroll-arrow-bottom {
  width: 100%;
  height: 20px;
  -webkit-border-top-left-radius: 3px;
          border-top-left-radius: 3px;
  -webkit-border-top-right-radius: 3px;
          border-top-right-radius: 3px; }

.muze-scroll-rect {
  float: left;
  position: relative; }

.muze-scroll-mover {
  position: absolute;
  -webkit-border-radius: 3px;
          border-radius: 3px;
  background: #a2a2a2;
  cursor: -webkit-grab;
  border-radius: 3px; }

.muze-scroll-mover:active {
  cursor: -webkit-grabbing; }

.muze-scroll-arrow-chevron {
  position: relative;
  text-align: center;
  padding: 1px;
  margin-bottom: 0px; }

.muze-scroll-arrow-chevron::before {
  content: '';
  position: absolute;
  background: white; }

.muze-scroll-arrow-chevron::after {
  content: '';
  position: absolute;
  background: white; }

#muze-scroll-arrow-chevron-bottom, #muze-scroll-arrow-chevron-top {
  height: 1px;
  width: 6px; }

#muze-scroll-arrow-chevron-left, #muze-scroll-arrow-chevron-right {
  width: 1px;
  height: 6px; }

#muze-scroll-arrow-chevron-bottom:before {
  top: 0;
  left: 0;
  height: 100%;
  width: 51%;
  -webkit-transform: skew(0deg, 35deg);
          transform: skew(0deg, 35deg); }

#muze-scroll-arrow-chevron-bottom:after {
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
  -webkit-transform: skew(0deg, -35deg);
          transform: skew(0deg, -35deg); }

#muze-scroll-arrow-chevron-top:before {
  top: 0;
  left: 0;
  height: 100%;
  width: 51%;
  -webkit-transform: skew(0deg, -35deg);
          transform: skew(0deg, -35deg); }

#muze-scroll-arrow-chevron-top:after {
  top: 0;
  right: 0;
  height: 100%;
  width: 50%;
  -webkit-transform: skew(0deg, 35deg);
          transform: skew(0deg, 35deg); }

#muze-scroll-arrow-chevron-right:before {
  top: 0;
  left: 0;
  height: 51%;
  width: 100%;
  -webkit-transform: skew(35deg, 0deg);
          transform: skew(35deg, 0deg); }

#muze-scroll-arrow-chevron-right:after {
  bottom: 0;
  left: 0;
  width: 100%;
  height: 50%;
  -webkit-transform: skew(-35deg, 0deg);
          transform: skew(-35deg, 0deg); }

#muze-scroll-arrow-chevron-left:before {
  bottom: 0;
